<script>
import routerObj from '@/contracts/contractRouter'
import { handleLeaderInfo, handleLeaderReward } from '@/services/teamLeader'
import { handleMining } from '@/services/warehourse'
import KolPop from './components/KolPop.vue'
export default {
  name: '',
  data () {
    return {
      myInfo: {},
      list: [],
      kolShow: false,
      sfoLoading: false,
      invietUrl: '',
      sfoPrice: 0
    }
  },
  methods: {
    // 首页信息
    async getInfo () {
      const resp = await handleLeaderInfo()
      if (!resp.success) { return }
      this.myInfo = resp.data
      this.invietUrl = location.origin + '/#/?inviteCode=' + this.myInfo.inviteCode
    },
    async getList () {
      const resp = await handleLeaderReward({
        orderByColumn: 'id',
        isAsc: 'desc'
      })
      if (!resp.success) return
      this.list = resp.rows
    },
    // 复制
    handleCopy (content) {
      const text = document.createElement('textarea')
      text.value = content
      document.body.appendChild(text)
      text.select() // 选择对象
      if (document.execCommand('copy')) {
        document.execCommand('copy')
        this.$toast('copied')
      }
      // 执行浏览器复制命令
      document.body.removeChild(text)
    },
    handleKolShow () {
      this.kolShow = !this.kolShow
    },
    // 获取header数据
    async getMining () {
      const resp = await handleMining()
      if (!resp.success) return
      this.handleSfoPrice(resp.data.supportTokenList[0].tokenAddress)
    },
    async handleSfoPrice (address) {
      this.sfoLoading = true
      const resp = await routerObj.getAmountsIn(
        address
      )
      this.sfoLoading = false
      if (!resp.success) return
      this.sfoPrice = resp.result || 0
      console.log('this.sfoPrice', this.sfoPrice)
    }
  },
  mounted () {
    this.getMining()
    this.getInfo()
    this.getList()
  },
  components: { KolPop }
}
</script>

