import web3 from '../utils/web3'
import kolAbi from '@/abi/kol.json'
import config from '@/config/app.config.js' // 引入配置文件
import store from '@/store'

const kolRights = config.kolRights
const kolContract = new web3.web3.eth.Contract(kolAbi, kolRights)

const app = async (tokenId) => {
  const currentAccount = store.state.myAccount
  try {
    var amount = await kolContract.methods.app(tokenId).send({
      from: currentAccount
    })
    return web3.successResult(amount)
  } catch (error) {
    return web3.failResult(error)
  }
}

const protect = async () => {
  try {
    var resp = await kolContract.methods.protect().call({})
    return web3.successResult(resp)
  } catch (error) {
    return web3.failResult(error)
  }
}

const routerObj = {
  protect,
  app
}
export default routerObj
