<template>
  <VanPopup
    style="
      box-sizing: border-box;
      width: 93%;
      max-width: 400px;
      background-color: #352c25;
      padding: 30px 45px;
      border-radius: 15px;
    "
    round
    @click-overlay="closePop"
    v-model="show"
  >
    <div class="kol-wrap">
      <div class="title">{{ $t('admin.kolTitle') }}</div>
      <div class="balance-name">{{ $t('admin.kolBalance') }}</div>
      <div class="balance" translate="no">
        $<span>{{ $gbUtils.formatBalance(uBalance) }}</span>
      </div>
      <div class="notice">
        <div>{{ $t('admin.kolNotice1') }}</div>
        <div>{{ $t('admin.kolNotice2') }}</div>
        <div>{{ $t('admin.kolNotice3') }}</div>
        <div>{{ $t('admin.kolNotice4') }}</div>
      </div>
      <VanButton
        v-if="!authNft"
        color="#f8bb5a"
        :loading="transferLoading"
        :disabled="!isProtect"
        class="kol-button"
        @click="handleApproe"
      >
        {{ $t('admin.authNft') }}
      </VanButton>
      <VanButton
        v-else
        color="#f8bb5a"
        :loading="transferLoading"
        :disabled="!isProtect"
        class="kol-button"
        @click="handleTransfer"
        >{{ $t('admin.apply') }}</VanButton
      >
    </div>
  </VanPopup>
</template>

<script>
import config from '@/config/app.config'
import { mapActions } from 'vuex'
import { queryPreSaleInfo } from '@/services/preSale'
import kolRights from '@/contracts/kolRights'
import contractNFT from '@/contracts/contractNFT'
import contractToken from '@/contracts/contractToken'
export default {
  name: '',
  data () {
    return {
      show: true,
      uBalance: 0,
      transferLoading: true,
      authNft: false,
      myInfo: {},
      isProtect: false
    }
  },
  methods: {
    ...mapActions(['changeLang']),
    async getInfo () {
      const resp = await queryPreSaleInfo({ type: 1 })
      if (!resp.success) return
      this.myInfo = resp.data
      this.getAllowance()
    },
    async getAllowance () {
      this.transferLoading = true
      const resp = await contractNFT.getApproved(this.myInfo.tokenId)
      if (!resp.success) return
      this.transferLoading = false
      this.authNft = resp.result.toUpperCase() === config.kolRights.toUpperCase()
      console.log(this.authNft)
    },
    async getBalance () {
      const resp = await contractToken.balanceOf(config.USDT, config.kolRights)
      console.log(resp)
      if (!resp.success) return
      this.uBalance = resp.result
    },
    async getProtect () {
      const resp = await kolRights.protect()
      this.isProtect = resp.result
    },
    async handleApproe () {
      this.transferLoading = true
      const resp = await contractNFT.approve(this.myInfo.tokenId)
      if (!resp.success) return
      this.transferLoading = false
      this.getAllowance()
    },
    async handleTransfer () {
      this.transferLoading = true
      const resp = await kolRights.app(this.myInfo.tokenId)
      this.transferLoading = false
      if (!resp.success) return
      this.closePop()
      this.$gbUtils.handleToPage('/home')
    },
    closePop () {
      this.$emit('close')
    }
  },
  mounted () {
    this.getProtect()
    this.getInfo()
    this.getBalance()
  }
}
</script>

